.printer-selection-container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 20px;
    max-width: 1200px; /* Increase the modal width */
    margin: 0 auto;
  }
  
  .printer-location, .printer-options-container {
    width: 48%;
  }
  
  .printer-location h2, .printer-options-container h2 {
    margin-bottom: 10px;
  }
  
  .printer-location .form-group, .printer-options-container .form-group {
    margin-bottom: 20px;
  }
  
  .printer-location label, .printer-options-container label {
    display: block;
    margin-bottom: 5px;
  }
  
  .printer-location .location-options, .printer-options-container .printer-options {
    width: 100%;
  }
  
  .location-option, .printer-option {
    margin-bottom: 10px;
  }
  
  .location-option label, .printer-option label {
    display: flex;
    align-items: center;
  }
  
  .location-option input, .printer-option input {
    margin-right: 10px;
  }
  
  .printer-status {
    margin-left: 10px;
    font-size: 0.9em;
  }
  
  .printer-status.active {
    color: green;
  }
  
  .printer-status.inactive {
    color: gray;
  }
  
  hr {
    border: 0;
    border-top: 1px solid #ccc;
    margin: 10px 0;
  }
  
  .divider {
    border-left: 1px solid #ccc;
    height: auto;
    margin: 0 10px;
  }