.supply-modal {
  max-height: 800px;
}

.supply-modal h2 {
  text-align: center;
  margin-bottom: 20px;
}

.content-container {
  display: flex;
  flex-direction: column;
}

.supply-form {
  flex: 1;
  margin-bottom: 20px;
}

.supply-container {
  display: flex;
  justify-content: space-between;
}

.supply-section {
  flex: 1;
  padding: 10px;
}

.supply-item {
  display: flex;
  align-items: flex-start;
  margin-bottom: 20px;
}

.supply-item .icon {
  margin-right: 10px;
  margin-top: 5px;
}

.input-group {
  display: flex;
  flex-direction: column;
}

.input-group label {
  font-weight: bold;
  margin-bottom: 5px;
}

.input-group input,
.input-group select {
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 5px;
}

.input-group span {
  font-size: 0.9em;
  color: #666;
}

.separator {
  width: 1px;
  background-color: #ccc;
  margin: 0 20px;
}

.separator-horizontal {
  height: 1px;
  background-color: #ccc;
  margin: 20px 0;
}

.button-group {
  display: flex;
  justify-content: space-between;
  gap: 10px; /* Add space between buttons */
}

.submit-supplies-button,
.manage-printers-button,
.submit-printer-button,
.back-to-supplies-button,
.add-location-button,
.submit-location-button,
.cancel-location-button {
  display: block;
  flex: 1; /* Ensure buttons take equal space */
  padding: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 20px;
}

.submit-supplies-button:hover,
.manage-printers-button:hover,
.submit-printer-button:hover,
.back-to-supplies-button:hover,
.add-location-button:hover,
.submit-location-button:hover,
.cancel-location-button:hover {
  background-color: #0056b3;
}

.cancel-location-button {
  background-color: #6c757d;
}

.cancel-location-button:hover {
  background-color: #5a6268;
}

.printer-management {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.printer-management form {
  margin-bottom: 20px;
}

.printer-management h3 {
  margin-bottom: 10px;
}

.printer-list ul {
  list-style-type: none;
  padding: 0;
}

.printer-list li {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.printer-list li span {
  font-weight: bold;
}

.printer-list li .status {
  color: #007bff;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}